<template>
    <div>
        <v-navigation-drawer v-model="drawer" app clipped>
            <v-list dense>
                <v-subheader class="mt-4 grey--text text--darken-1">THEME</v-subheader>
                <v-list-item link @click="toggleTheme">
                    <v-list-item-action>
                        <v-icon v-if="isDark">far fa-check-square</v-icon>
                        <v-icon v-else>far fa-square</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                        <v-list-item-title>Dark</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
<!--
                <v-subheader class="mt-4 grey--text text--darken-1">GAMES</v-subheader>
                <v-list-item link @click="toggleDiscount">
                    <v-list-item-action>
                        <v-icon v-if="isDiscountOnly">far fa-check-square</v-icon>
                        <v-icon v-else>far fa-square</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                        <v-list-item-title>Discount Only</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
-->
                <v-subheader class="mt-4 grey--text text--darken-1">ORDER PRIORITY</v-subheader>
                <v-list>
                    <v-list-item v-for="priority in sortPriorities" :key="priority.sort" link @click="changePriority(priority.sort)">
                        <v-list-item-action>
                            <v-icon v-if="conditions.sortPriority === priority.sort">far fa-check-circle</v-icon>
                            <v-icon v-else>far fa-circle</v-icon>
                        </v-list-item-action>
                        <v-list-item-content>
                            <v-list-item-title>{{ priority.sort }}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                </v-list>
                
                <v-subheader class="mt-4 grey--text text--darken-1">OPTIONAL</v-subheader>
                <v-list-item link @click="toggleScreen">
                    <v-list-item-action>
                        <v-icon v-if="isScreenshot">far fa-check-square</v-icon>
                        <v-icon v-else>far fa-square</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                        <v-list-item-title>SCREENSHOT</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
                <v-list-item link @click="togglePeriod">
                    <v-list-item-action>
                        <v-icon v-if="isPeriod">far fa-check-square</v-icon>
                        <v-icon v-else>far fa-square</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                        <v-list-item-title>PERIOD</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
                <v-list-item link @click="toggleHistory">
                    <v-list-item-action>
                        <v-icon v-if="isHistory">far fa-check-square</v-icon>
                        <v-icon v-else>far fa-square</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                        <v-list-item-title>HISTORY</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-navigation-drawer>

        <v-app-bar app clipped-left color="red" dense>
            <v-app-bar-nav-icon @click.stop="drawer = !drawer" />
            <v-toolbar-title class="mr-12 align-center" style="padding-left:0px !important;">
                <span class="title font-weight-light">SWITCH SHOPPER</span>
            </v-toolbar-title>
            <v-spacer />

            <v-progress-circular v-if="isLoading" indeterminate />
            <v-icon v-else class="mx-4" style="margin-right: 0px !important;" @click="openBottomSearcher">fas fa-search</v-icon>
        </v-app-bar>
    </div>
</template>

<script>
export default {
    props: [ 'conditions' ],
    data: () => ({
        drawer: null,
        isDark: false,
        isDiscountOnly: true,
        sortPriorities: [
            { sort: 'DATE', checked: false },
            { sort: 'PRICE', checked: false },
            { sort: 'PERCENT', checked: false }
        ],
        isScreenshot: true,
        isPeriod: true,
        isHistory: true,
        isLoading: false
    }),
    created () {
        this.sortPriorities.filter(sp => sp.sort == this.conditions.sortPriority)
            .forEach(sp => sp.checked = true)

        this.isScreenshot = this.conditions.screen
        this.isPeriod = this.conditions.period
        this.isHistory = this.conditions.history
    },
    methods: {
        toggleTheme () {
            this.isDark = !this.isDark
            this.$vuetify.theme.dark = this.isDark
            this.$emit('toggle-is-dark', this.isDark)
        },
        toggleDiscount () {
            this.isDiscountOnly = !this.isDiscountOnly
            this.$emit('toggle-discount', this.isDiscountOnly)
        },
        changePriority (sort) {
            if (this.conditions.sortPriority != sort)
                this.$emit('change-priority', sort)
        },
        toggleScreen () {
            this.isScreenshot = !this.isScreenshot
            this.$emit('toggle-screen', this.isScreenshot)
        },
        togglePeriod () {
            this.isPeriod = !this.isPeriod
            this.$emit('toggle-period', this.isPeriod)
        },
        toggleHistory () {
            this.isHistory = !this.isHistory
            this.$emit('toggle-history', this.isHistory)
        },
        openBottomSearcher () {
            this.$emit('open-bottom-searcher')
        }
    },
}
</script>

