<template>
    <v-card :elevation="5" @click="openDetails(game)">
        <v-img v-if="conditions.screen" :src="hero" class="white--text align-end" gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)" height="200px">
            <v-card-title v-text="game.title" class="game-title"></v-card-title>
        </v-img>
        <v-card-title v-if="!conditions.screen" class="one-line">{{ game.title }}</v-card-title>
        <v-card-subtitle dense>{{ game.categoryText }}</v-card-subtitle>
        <v-card-text dense>{{ game.numberOfPlayers }}</v-card-text>

        <v-divider></v-divider>

        <v-list-item dense>
            <v-list-item-icon style="margin-right: 0px !important;">
                <v-icon x-small>fas fa-dollar-sign</v-icon>
            </v-list-item-icon>
            <v-list-item-subtitle>{{ game.currentPriceText }} <span v-if="game.isDiscountPresent">(<strike>{{ formated(game.price) }}</strike> / {{ game.percentText }}%)</span></v-list-item-subtitle>
            <v-spacer/>
            <v-list-item-action-text align="right" style="width: 300px;">{{ game.dayLeft }}</v-list-item-action-text>
        </v-list-item>

        <v-list-item v-if="conditions.period && game.isDiscountPresent" dense>
            <v-list-item-icon style="margin-right: 0px !important;">
                <v-icon x-small>fas fa-calendar-alt</v-icon>
            </v-list-item-icon>
            <v-list-item-subtitle>Discount Period</v-list-item-subtitle>
            <v-spacer/>
            <v-list-item-action-text align="right" style="width: 300px;">{{ game.discountPeriod }}</v-list-item-action-text>
        </v-list-item>

        <v-list-item v-if="conditions.period && !game.isDiscountPresent" dense>
            <v-list-item-icon style="margin-right: 0px !important;">
                <v-icon x-small>fas fa-calendar-alt</v-icon>
            </v-list-item-icon>
            <v-list-item-subtitle>Release Date</v-list-item-subtitle>
            <v-spacer/>
            <v-list-item-action-text align="right" style="width: 300px;">{{ game.releaseDate }}</v-list-item-action-text>
        </v-list-item>

        <div v-if="conditions.history && game.discounts.length > 0">
            <v-divider></v-divider>
            <game-sparkline :price="game.price" :discounts="game.discounts" />
        </div>
  
    </v-card>
</template>

<script>
import GameSparkline from './GameSparkline'
import noImage from '../assets/no-image.png'
import numeral from 'numeral'

export default {
    props: [ 'game', 'conditions' ],
    data: () => ({

    }),
    computed: {
        hero () {
            return (this.game.hero) ? `https://nintendo.com${this.game.hero}` : noImage
            // let screens = this.game.screens
            // return (!!screens && screens.length > 0) ? screens[0] : noImage
        }
    },
    methods: {
        openDetails (game) {
            this.$emit('open-details', game)
        },
        formated (price) {
            return numeral(price).format('0.00')
        }
    },
    components: {
        'game-sparkline': GameSparkline
    }
}
</script>

<style scoped>
.game-title {
    text-shadow: 0 0 2px #000, 0 0 10px #fff;
}
.one-line {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}
</style>