<template>
    <v-dialog v-model="openDetails" fullscreen hide-overlay transition="dialog-bottom-transition">
        <v-card>
            <v-toolbar dark color="red">
                <v-btn icon dark @click="closeDetails">
                    <v-icon>mdi-close</v-icon>
                </v-btn>
                <v-toolbar-title style="padding-left: 0px !important;">Game Details</v-toolbar-title>
            </v-toolbar>
            <div style="display: none">
                <img v-for="(screen, idx) in game.screens" :src="screen" :key="idx" />
            </div>
            <v-carousel cycle interval="3000" height="auto">
                <v-carousel-item v-for="(screen, idx) in game.screens" :src="screen" :key="idx" />
            </v-carousel>

            <v-list three-line subheader>
                <v-list-item>
                    <v-list-item-content>
                        <v-list-item-title>Title</v-list-item-title>
                        <v-list-item-subtitle>{{ game.title }}</v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>
                <v-list-item>
                    <v-list-item-content>
                        <v-list-item-title>Category</v-list-item-title>
                        <v-list-item-subtitle>
                            <v-chip v-for="(category, idx) in game.categories" :key="idx" class="ma-2" x-small dense>{{ category }}</v-chip>
                        </v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>
                <v-list-item>
                    <v-list-item-content>
                        <v-list-item-title>Current Price</v-list-item-title>
                        <v-list-item-subtitle>$ {{ game.currentPriceText }} <span v-if="game.isDiscountPresent">(<strike>{{ game.price }}</strike> / {{ game.percentText }}%)</span></v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>
                <v-list-item>
                    <v-list-item-content>
                        <v-list-item-title>Number of Player</v-list-item-title>
                        <v-list-item-subtitle>{{ game.numberOfPlayers }}</v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>
                <v-list-item>
                    <v-list-item-content>
                        <v-list-item-title>Release Date</v-list-item-title>
                        <v-list-item-subtitle>{{ game.releaseDate }}</v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>
                <v-list-item>
                    <v-list-item-content>
                        <v-list-item-title>Slug</v-list-item-title>
                        <v-list-item-subtitle><a :href="goToSlug(game.slug)" target="_blank">{{ goToSlug(game.slug) }}</a></v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
            
            <v-divider></v-divider>

            <v-list v-if="discounts.length > 0" flat>
                <v-subheader>Discount History</v-subheader>
                <v-list-item-group color="primary">
                    <v-list-item dense v-for="(discount, idx) in discounts" :key="idx">
                        <v-list-item-icon style="margin-right: 10px;">
                            <v-icon :color="discount.directionColor" small>fas {{ discount.direction }}</v-icon>
                        </v-list-item-icon>
                        <v-list-item-content>
                            <v-list-item-title>$ {{ discount.price }} ({{ calcPercent(discount.price) }}%)</v-list-item-title>
                            <v-list-item-subtitle>{{ calcDate(discount.startDateTime) }} ~ {{ calcDate(discount.endDateTime) }}</v-list-item-subtitle>
                        </v-list-item-content>
                    </v-list-item>
                </v-list-item-group>
            </v-list>

            <v-divider></v-divider>
        </v-card>
    </v-dialog>
</template>

<script>
import moment from 'moment'
import numeral from 'numeral'

const TIME_DIFF = 9

export default {
    props: [ 'game', 'openDetails' ],
    computed: {
        discounts () {
            let basePrice = this.game.price
            if (this.game.discounts) {
                return this.game.discounts.map(discount => {
                    let direction = ''
                    let directionColor = ''
                    if (basePrice < discount.price) {
                        direction = 'fa-arrow-up'
                        directionColor = 'red'
                    } else if (basePrice == discount.price) {
                        direction = 'fa-minus'   
                        directionColor = 'grey'
                    } else {
                        direction = 'fa-arrow-down'
                        directionColor = 'blue'
                    }
                    basePrice = discount.price

                    return {
                        directionColor,
                        direction, 
                        price: discount.price,
                        startDateTime: this.calcDate(discount.startDateTime),
                        endDateTime: this.calcDate(discount.endDateTime)
                    }
                })
            } else {
                return []
            }
        },
    },
    methods: {
        calcPercent (discountPrice) {
            return numeral((1 - discountPrice / this.game.price) * 100).format('0')
        },
        calcDate (date) {
            return moment(date, 'YYYY-MM-DD HH:mm:ss').add(TIME_DIFF, 'hours').format('YYYY-MM-DD')
        },
        goToSlug (slug) {
            return `https://www.nintendo.com/games/detail/${slug}`
        },
        closeDetails () {
            this.$emit('close-details')
        }
    }
}
</script>

